.transactionContainer {
  border-collapse: collapse;
  /* display: block; */
  margin: 0 auto;
  width: 500px;

  border: 1px solid black;
  background-color: white;
}
.transactionContainer th {
  border-collapse: collapse;
  height: 40px;
  border: 1px solid black;
}
.transactionContainer td {
  border-collapse: collapse;
  height: 40px;
  border: 1px solid black;
}
